.hero__btn {
  padding: 14px 78px;
  border: 2px solid transparent;
  border-radius: 50px;
  background-color: $main-accent-color;
  color: $primary-white-color;
  font-weight: 600;
  font-size: 15px;
  line-height: 1.33;
  transition: color $cubic, background-color $cubic, border $cubic;
  &:hover, &:focus {
    border: 2px solid $main-accent-color;
    background-color: $primary-white-color;
    color:  $main-accent-color;
  }
    @media screen and (min-width: 768px) {
      padding: 14px 22px;
    }
  
    @media screen and (min-width: 1440px) {
    padding: 16px 24px;
    font-size: 18px;
    line-height: 1.39;
    }
}

.hero__title {
  max-width: 335px;
  margin-bottom: 16px;
  font-family: 'Montserrat', sans-serif;
  font-weight:700;
  line-height: 1.15;
  font-size: 26px;
  @media screen and (min-width: 768px) {
    max-width: 453px;
    font-size: 36px;
  }
  @media screen and (min-width: 1440px) {
    max-width: 678px;
    font-size: 55px;
    margin-bottom: 20px;
  }
  span {
    color: $main-accent-color;
  }
}

.hero__text {
  font-weight: 400;
  max-width: 335px;
  font-size: 16px;
  line-height: 1.38;
  margin-bottom: 24px;
  @media screen and (min-width: 768px) {
      max-width: 453px;
  }
  @media screen and (min-width: 1440px) {
      max-width: 604px;
      font-size: 18px;
      line-height: 1.39;
      margin-bottom: 40px;
  }
  span {
    font-weight: 700;
  }
}

.hero {
  position: relative;
}

.container--thumb-container {
  position: relative;
}

.hero__container {
  // position:relative;
  height: 596px;
  
  @media screen and (min-width: 768px) {
      height: 817px;
  }
      
  @media screen and (min-width: 1440px) {
      height: 564px;
  }

}

.hero__image {
  position: absolute;
  background-image: url(../images/hero/hero_mobile.svg);
  background-repeat: no-repeat;
  width: 425px;
  height: 285px;
  left:50%;
  bottom:32px;
  
  @media screen and (max-width: 1439px) {
    transform: translate(-50%, 0%);
  }
 
  @media screen and (min-width: 768px) {
    background-image: url(../images/hero/hero_tablet.svg);
    width: 738px;
    height: 495px;
    bottom: 0;
  }
     
   @media screen and (min-width: 1440px) {
    background-image: url(../images/hero/hero.svg);
    // bottom: 88px;
    width: 836px;
    height: 561px;
    right:0px;
    top:100%;
    
  }
}